                    "
                  >
                    {{ item.term }}
                  </span>
                  {{ "( " + item.level + " ) " }}
                </span>
              </el-tooltip>
            </div>
            <div class="tabPaneBox">
              <el-table
                :data="musicItem.musicItemTableData[index]"
                border
                height="130px"
                style="width: 100%"
              >
                <el-table-column
