                  :show-overflow-tooltip="true"
                ></el-table-column>
                <el-table-column
                  prop="holdNumber"
                  label="hold数量"
                  :show-overflow-tooltip="true"
                ></el-table-column>
                <el-table-column
                  prop="slideNumber"
                  label="slide数量"
                  :show-overflow-tooltip="true"
                ></el-table-column>
                <el-table-column
                  prop="touchNumber"
