                ></el-table-column>
                <el-table-column
                  prop="breakNumber"
                  label="break数量"
                  :show-overflow-tooltip="true"
                ></el-table-column>
              </el-table>
              <div style="margin-top: 10px">
                <el-form ref="form" inline label-width="0px">
                <el-form-item
                  style="margin: 0; width: 100%"
                  v-if="
                    musicItem.musicNickName && musicItem.musicNickName.length
                  "
                >
                  {{
                    musicItem.charts[index].charter === "-"
                      ? "当前铺面谱师不详"
                      : "当前铺面谱师为：" + musicItem.charts[index].charter
                  }}，此歌曲在民间又被称为：{{ musicItem.musicNickName }}
                </el-form-item>
                <el-form-item style="margin: 0; width: 100%">
                  {{ dialogTitle }}
                  <span
                    :class="
                      'ft maiColor' +
                      (item.eName === 'Re:Master' ? 'ReMaster' : item.eName)
                    "
                  >
                    {{ "( " + item.term + " ) " }}
                  </span>
                  理论 (101%) 每种类型 NOTE 单个占比(仅供参考)：
                </el-form-item>

                <!-- TAP、TOUCH -->
                <el-form-item style="margin-bottom: 10px; width: 100%;">
                  <p>Tap/Touch音符：</p>
                  <p class="ft maiP">Perfect：{{musicItem.musicItemTableData[index][0].maiTapTable[0].y3}}</p>
                  <p class="ft maiG">Great：{{musicItem.musicItemTableData[index][0].maiTapTable[0].p3}}</p>
                  <p class="ft maiG2">Good：{{musicItem.musicItemTableData[index][0].maiTapTable[0].g3}}</p>
                </el-form-item>

                <!-- HOLD -->
                <el-form-item style="margin-bottom: 10px; width: 100%;">
                  <p>Hold音符：</p>
                  <p class="ft maiP">Perfect：{{musicItem.musicItemTableData[index][0].maiHoldTable[0].y3}}</p>
                  <p class="ft maiG">Great：{{musicItem.musicItemTableData[index][0].maiHoldTable[0].p3}}</p>
                  <p class="ft maiG2">Good：{{musicItem.musicItemTableData[index][0].maiHoldTable[0].g3}}</p>
                </el-form-item>

                <!-- SLIDE -->
                <el-form-item style="margin-bottom: 10px; width: 100%;">
                  <p>Slide音符：</p>
                  <p class="ft maiP">Perfect：{{musicItem.musicItemTableData[index][0].maiSlideTable[0].y3}}</p>
                  <p class="ft maiG">Great：{{musicItem.musicItemTableData[index][0].maiSlideTable[0].p3}}</p>
                  <p class="ft maiG2">Good：{{musicItem.musicItemTableData[index][0].maiSlideTable[0].g3}}</p>
                </el-form-item>

                <!-- BREAK -->
                <el-form-item style="margin-bottom: 10px; width: 100%;">
                  <p>Break：</p>
                  <p class="ft maiCP">大P(理论)：{{musicItem.musicItemTableData[index][0].maiBreakTable[0].y3}}</p>
                  <p class="ft maiP">小P(50落)：{{musicItem.musicItemTableData[index][0].maiBreakTable[0].y6}}</p>
                  <p class="ft maiP">小P(100落)：{{musicItem.musicItemTableData[index][0].maiBreakTable[0].y9}}</p>
                  <p class="ft maiG">大粉：{{musicItem.musicItemTableData[index][0].maiBreakTable[0].p3}}</p>
                  <p class="ft maiG">小粉(50落)：{{musicItem.musicItemTableData[index][0].maiBreakTable[0].p6}}</p>
                  <p class="ft maiG">小粉(100落)：{{musicItem.musicItemTableData[index][0].maiBreakTable[0].p9}}</p>
                  <p class="ft maiG2">绿了：{{musicItem.musicItemTableData[index][0].maiBreakTable[0].g3}}</p>
