          >登录并HACK</el-button
        >
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 引入 music 数据，其中 notes 数据顺序为：tap hold slide touch break
import musicData from "../assets/musicData/music.js";
import musicNickName from "../assets/musicNickName/nickName.js";
import musicCoverData from "../assets/musicData/maidata.js";

import $ from "jquery";
import { clearInterval, setInterval } from "timers";
import { Message } from "element-ui";

export default {
  data() {
    return {
      isMobileUser: this.isMobile(), // 判断是否是手机访问
      cityName: "", // 当前用户定位
      pagesize: 10, // 翻页器参数，每页条数
      currentPage: 1, // 翻页器参数，当前页数

      showDialog: false, // PC 端详情弹窗开关
      showDrawer: false, // 移动端详情弹窗开关
      tabsActveIndex: "0", // 详情 tabs 分页切换的页数 name
      musicItem: {
        // 查看详情的表格回调项
        maiLevelName: [],
        ds: [],
        musicItemTableData: [],
      },

      dialogTitle: "详情", // 查看详情弹窗的标题

      musicTypeArray: [
        {
          // 乐曲类型选择器数据
          label: "DX乐谱",
          value: "DX",
        },
        {
          label: "标准乐谱",
          value: "SD",
        },
      ],

      musicLevelArray: [], // 乐曲级别选择器数据

      searchForm: {
        // 其他搜索条件
        id: "", // 乐曲 ID
        title: "", // 乐器名称
        ds: 0, // 乐曲定数
        type: "", // 乐曲类型
        level: "", // 乐曲级别
        noobNickName: "", // 乐曲别名
      },

      viewerShow: false, // 查看歌曲封面弹窗显示开关
      previewCoverUrl: [], // 所查看的封面

      tableData: [], // 乐曲主体数据

      showCbkDialog: false, // 彩贝壳表单弹窗显示开关
      cbkMobile: "", // 彩贝壳手机号
      cbkSmsCode: "", // 彩贝壳验证码
    };
  },

  mounted() {
    this.$nextTick(() => {
      // const loading = this.$loading({
      //   lock: true,
      //   text: "正在拼了老命的加载中...请稍等...",
      //   spinner: "el-icon-loading",
      //   background: "rgba(0, 0, 0, 0.7)",
      // });

      // loading.close();

      this.riseData();

      musicData.sort((a, b) => {
        return a.id - b.id;
      });

      this.tableData = JSON.parse(JSON.stringify(musicData));

      this.renderSearchOption();

      // 将乐曲别名数据 value 统一转为字符串，防止有数字掺杂导致搜索不出
      musicNickName.forEach((ele) => {
        for (let key in ele) {
          ele[key] += "";
        }
      });

      // $.getScript("https://pv.sohu.com/cityjson?ie=utf-8", () => {
      //   this.cityName = returnCitySN.cname;
      // });
      // let player = GetURLParameter('player');
    });
  },

  methods: {
    //是移动端返回true,否则false
    isMobile() {
      return /phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone|webOS|android/i.test(
        navigator.userAgent
      );
    },

    // 取随机数
    randomNum(minNum, maxNum) {
      switch (arguments.length) {
        case 1:
          return parseInt(Math.random() * minNum + 1, 10);
          break;
        case 2:
          return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
          break;
        default:
          return 0;
          break;
      }
    },

    // 获取 tap 数据
    getTap(baseScore) {
      return [
        {
          name: "Perfect",
          score: baseScore,
          proportion: (baseScore / 10000).toFixed(4),
          className: "maiP",
        },
        {
          name: "Great",
          score: baseScore * 0.8,
          proportion: ((baseScore * 0.8) / 10000).toFixed(4),
          className: "maiG",
        },
        {
          name: "Good",
          score: baseScore * 0.5,
          proportion: ((baseScore * 0.5) / 10000).toFixed(4),
          className: "maiG2",
        },
      ];
    },

    // 获取 hold 数据
    getHold(baseScore) {
      let hold = this.getTap(baseScore);
      hold.forEach((ele) => {
        ele.score *= 2;
        ele.proportion = (ele.proportion * 2).toFixed(4);
      });
      return hold;
    },

    // 获取 slide 数据
    getSlide(baseScore) {
      let slide = this.getTap(baseScore);
      slide.forEach((ele) => {
        ele.score *= 3;
        ele.proportion = (ele.proportion * 3).toFixed(4);
      });
      return slide;
    },

    // 获取 break 数据
    getBreak(breakNumber, baseScore) {
      const totalScore = 10000 / breakNumber + baseScore * 5;
      return {
        Perfect: [
          {
            name: "Critical Perfect",
            score: totalScore,
            proportion: (totalScore / 10000).toFixed(4),
            className: "maiCP",
          },
          {
            name: "Lost 50 Perfect",
            score: totalScore * 0.8,
            proportion: ((totalScore * 0.8) / 10000).toFixed(4),
            className: "maiP",
          }, // 2550
          {
            name: "Lost 100 Perfect",
            score: totalScore * 0.5,
            proportion: ((totalScore * 0.5) / 10000).toFixed(4),
            className: "maiP",
          }, // 2500
        ],
        Great: [
          {
            name: "Break Great",
            score: totalScore * 0.4,
            proportion: ((totalScore * 0.4) / 10000).toFixed(4),
            className: "maiG",
          }, // 2000
          {
            name: "Lost 500 Great",
            score: totalScore * 0.3,
            proportion: ((totalScore * 0.3) / 10000).toFixed(4),
            className: "maiG",
          }, // 1500
          {
            name: "Lost 750 Great",
            score: totalScore * 0.25,
            proportion: ((totalScore * 0.25) / 10000).toFixed(4),
            className: "maiG",
          }, // 1250
        ],
        Good: [
          {
            name: "Break Good",
            core: totalScore * 0.2,
            proportion: ((totalScore * 0.2) / 10000).toFixed(4),
            className: "maiG2",
          },
        ],
      };
    },

    // 获取 touch 数据
    getTouch(baseScore) {
      return [
        {
          name: "Perfect",
          score: baseScore,
          proportion: (baseScore / 10000).toFixed(4),
          className: "maiP",
        },
        {
          name: "Late",
          score: baseScore * 0.5,
          proportion: (baseScore / 10000).toFixed(4),
          className: "maiG2",
        },
      ];
    },

    // 获取 touchHold 数据
    getTouchHold(baseScore) {
      let touchHold = this.getTouch(baseScore);
      touchHold.forEach((ele) => {
        ele.score *= 2;
        ele.proportion *= 2;
      });
      return touchHold;
    },

    // 获取级别名称和术语
    getMaiLevelName() {
      return [
        { eName: "Basic", cName: "初级", term: "绿谱" },
        { eName: "Advanced", cName: "高级", term: "黄谱" },
        { eName: "Expert", cName: "专家", term: "红谱" },
        { eName: "Master", cName: "大师", term: "紫谱" },
        { eName: "Re:Master", cName: "宗师", term: "白谱" },
      ];
    },

    // 修改每页显示数量
    handleSizeChange(size) {
      this.pagesize = size;
    },

    // 修改页数
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
    },

    // 清洗数据
    riseData() {
      const maiLevelNameData = this.getMaiLevelName();
      musicData.forEach((pEle) => {
        pEle.coverUrl =
          this.cityName.indexOf("宁夏") !== -1
            ? "./static/img/qz-" + this.randomNum(1, 8) + ".jpg"
            : "./static/img/akalin.png";

        let maiSegaMusicItem = musicCoverData.find((coverEle) => {
          return (
            String(pEle.title).toLowerCase() ===
            String(coverEle.title).toLowerCase()
          );
        });

        if (maiSegaMusicItem && maiSegaMusicItem.image_file) {
          // pEle.coverUrl = "https://maimai.sega.com/storage/DX_jacket/" + maiSegaMusicItem.image_url;
          pEle.coverUrl =
            "https://maimai.wahlap.com/maimai-mobile/img/Music/" +
            maiSegaMusicItem.image_file;
        }

        let levelNameArray = [];
        pEle.maiDs = pEle.ds.toString().replace(/,/g, " -> ");
        pEle.maiLevel = pEle.level.toString().replace(/,/g, " -> ");

        pEle.level.forEach((cEle, cIndex) => {
          levelNameArray.push({
            level: cEle,
            ds: pEle.ds[cIndex],
            eName: maiLevelNameData[cIndex].eName,
            cName: maiLevelNameData[cIndex].cName,
            term: maiLevelNameData[cIndex].term,
          });
        });

        pEle.maiLevelName = levelNameArray;

        let musicNickNameItem = musicNickName.find((ele) => {
          return ele["歌曲名称"] === pEle.title;
        });

        if (musicNickNameItem) {
          let musicNickName = [];
          for (let key in musicNickNameItem) {
            if (key !== "歌曲名称" && musicNickNameItem[key]) {
              musicNickName.push(musicNickNameItem[key]);
            }
          }
          pEle.musicNickName = musicNickName;
        }
      });
    },

    // 搜索歌曲数据
    searchMusicData() {
      let filters = []; // 搜索条件
      this.currentPage = 1;

      // 如果填入了 ID
      if (this.searchForm.id) {
        filters.push({
          field: "id",
          relationType: "equal",
          value: this.searchForm.id,
        });
      }

      // 如果填入了乐曲名称
      if (this.searchForm.title) {
        filters.push({
          field: "title",
          relationType: "like",
          value: this.searchForm.title,
        });
      }

      // 如果填入了乐曲别名
      if (this.searchForm.noobNickName) {
        filters.push({
          field: "musicNickName",
          relationType: "include",
          value: this.searchForm.noobNickName,
        });
      }

      // 如果填入了定数
      if (this.searchForm.ds && this.searchForm.ds !== "->") {
        filters.push({
          field: "ds",
          relationType: "include",
          value: this.searchForm.ds + "",
        });
      }

      // 如果填入了乐曲类型
      if (this.searchForm.type) {
        filters.push({
          field: "type",
          relationType: "equal",
          value: this.searchForm.type,
        });
      }

      // 如果填入了乐曲等级
      if (this.searchForm.level) {
        filters.push({
          field: "level",
          relationType: "include",
          value: this.searchForm.level,
        });
      }

      let tableData = JSON.parse(JSON.stringify(musicData));
      this.tableData = this.filteringDataSources(tableData, filters);
      this.riseData();
    },

    // 搜索算法
    filteringDataSources(source, filters) {
      // 动态表达式集合，用于存储判断某个对象是否满足条件的函数
      const expressions = [];
      // 遍历过滤器集合，动态添加表达式函数
      filters.forEach((item) => {
        // 添加表达式函数，参数为数组的每个数据对象
        expressions.push((obj) => {
          // 是否符合条件
          let isFit = false;
          // 数据对象对应的属性值
          let objValue = "";
          // 用于被比较的过滤器值
          const compareValue = item.value;
          // 判断数据对象是否存在用于过滤的属性，如果不存在直接判定为不符合条件
          if (typeof obj[item.field] === "undefined") {
            isFit = false;
            return isFit;
          }

          // 获取数据对象用于比较的属性值，统一转为字符串类型，便于比较
          objValue = String(obj[item.field]);

          // 判断逻辑
          if (item.relationType === "equal") {
            // 等于
            isFit = objValue.toLowerCase() === compareValue.toLowerCase();
          } else if (item.relationType === "notEqual") {
            // 不等于
            isFit = objValue.toLowerCase() !== compareValue.toLowerCase();
          } else if (item.relationType === "like") {
            // 模糊匹配
            isFit = objValue.toLowerCase().includes(compareValue.toLowerCase());
          } else if (item.relationType === "beginWith") {
            // 以它开头
            isFit = objValue
              .toLowerCase()
              .startsWith(compareValue.toLowerCase());
          } else if (item.relationType === "endWith") {
            // 以它结尾
            isFit = objValue.toLowerCase().endsWith(compareValue.toLowerCase());
          } else if (item.relationType === "include") {
            // 包含(用于数组筛查)
            isFit = objValue.split(",").some((ele) => {
              return ele.toLowerCase() === compareValue.toLowerCase();
            });
          }

          // 返回当前表达式是否符合条件
          return isFit;
        });
      });

      // 遍历数据源
      source = source.filter((item, index) => {
        // 是否符合条件
        let isFit = true;
        // 遍历表达式集合，循环判断每个用于过滤的表达式函数是否符合
        for (let index = 0; index < expressions.length; index++) {
          // 获取表达式函数
          const expression = expressions[index];
          // 调用表达式函数，获取结果
          const result = expression(item);
          // 如果结果为false，则终止表达式集合的遍历（即有一个条件不符合，则该条数据则被判定不满足条件）
          if (!result) {
            isFit = false;
            break;
          }
        }

        // 返回当前数据对象是否符合条件，不符合条件则被过滤掉，不会出现在最终数据中
        return isFit;
      });

      // 返回过滤后的数据源
      return source;
    },

    // 生成其他搜索条件
    renderSearchOption() {
      // 乐曲等级搜索条件
      let musicLevelArray = [];

      musicData.forEach((pEle) => {
        // 包装乐曲等级搜索条件
        const levelFindResult = pEle.level.find((findEle) => {
          const cRes = musicLevelArray.some((cEle) => {
            return cEle === findEle;
          });
          return !cRes;
        });
        // 遍历出不重复的等级搜索条件并 push 进数组备用
        levelFindResult && musicLevelArray.push(levelFindResult);
      });

      // 将包装出来的等级搜索条件排序为从小到大的排列方式，如：
      // ["1", "2", "3", "4", "5", "6", "7", "7+", "8", "8+" ......]
      musicLevelArray.sort((a, b) => {
        const tempA = a.indexOf("+") !== -1 ? parseInt(a) + 0.5 : parseInt(a);
        const tempB = b.indexOf("+") !== -1 ? parseInt(b) + 0.5 : parseInt(b);
        return tempA - tempB;
      });

      // 遍历改变结构为 select 组件直接可用的方便格式
      musicLevelArray = musicLevelArray.map((ele) => {
        return {
          label: ele,
          value: ele,
        };
      });

      // 返回给 select 组件使用
      this.musicLevelArray = musicLevelArray;
    },

    // 查看详情
    viewDetail(musicItem) {
      console.log(123, musicItem);
      this.dialogTitle =
        "[ " + musicItem.title + " ( " + musicItem.type + " ) ]";
      let musicItemTableData = [];
      musicItem.charts.forEach((pEle, pIndex) => {
        const maiNotes = pEle.notes;
        const tapNumber = maiNotes[0];
        const holdNumber = maiNotes[1];
        const slideNumber = maiNotes[2];
        const touchNumber = musicItem.type === "DX" ? maiNotes[3] : 0;
        const breakNumber = musicItem.type === "DX" ? maiNotes[4] : maiNotes[3];

        const baseScore =
          1000000 /
          (tapNumber + touchNumber + breakNumber * 5 + holdNumber * 2 + slideNumber * 3);

        // tap note
        const maiTap = this.getTap(baseScore);
        // hold note
        const maiHold = this.getHold(baseScore);
        // slide note
        const maiSlide = this.getSlide(baseScore);
        // touch note
        const maiTouch = this.getTouch(baseScore);
        // toucHold note
        const maiTouchHold = this.getTouchHold(baseScore);
        // break note
        let maiBreak = this.getBreak(breakNumber, baseScore);

        console.log(
          musicItem.maiLevelName[pIndex].cName + "maiBreak",
          maiBreak
        );

        musicItemTableData.push([
          {
            tapNumber,
            holdNumber,
            slideNumber,
            touchNumber,
            breakNumber,

            maiTap,
            maiTapTable: [
              {
                y1: maiTap[0].name,
                y2: maiTap[0].className,
