      console.log(123123, musicItem);
    },

    // 谱面搜索
    goToFuckingBilibili(musicItem, levelName) {
      let keyWrod = "maimai " + musicItem.title + " " + levelName;
      if (this.isMobileUser) {
        window.open(`https://m.bilibili.com/search?keyword=${keyWrod}`);
      } else {
        window.open(
          `https://search.bilibili.com/all?keyword=${keyWrod}&from_source=web_search`
        );
      }
    },

    // 图片查看组件初始化
    viewerInited(viewer) {
      this.viewer = viewer;
      this.viewer.hidden = () => {
        this.viewerShow = false;
        if (this.isMobileUser) {
          document.body.classList.remove("viewer-open");
        }
      };
      this.viewer.view(0);
    },

    // 查看歌曲大图封面
    previewImage(previewCoverUrl) {
      this.previewCoverUrl = [previewCoverUrl];
      this.viewerShow = true;
    },

    // 设置 Cookie
    setCookie(name, value, exdays, domain) {
      //name  :表示cookie的名称，比如token
      //value  :表示cookie的值
      //exdays  :表示cookie的有效时间
      var d = new Date();
      d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
      var expires = "expires=" + d.toGMTString();
      if (domain) {
        document.cookie =
          name + "=" + value + "; " + expires + "; domain=" + domain + ";";
      } else {
        document.cookie = name + "=" + value + "; " + expires + "; ";
      }
    },

    // 弹出彩贝壳购买提示
    showCkbTips() {
      this.showCkbTipsTime = 0;
      clearInterval(this.showCkbTipsTimeer);
      this.showCkbTipsTimeer = null;
      this.showCkbTipsTimeer = setInterval(() => {
        this.showCkbTipsTime++;
        if (this.showCkbTipsTime >= 5) {
          this.resetCkbTips();
          const h = this.$createElement;
          this.$msgbox({
            title: "一些提示信息",
            showClose: false,
            distinguishCancelAndClose: true,
            message: h("div", null, [
              h(
                "p",
                { style: "color: #F25656; margin-bottom: 5px;" },
                "当前理论购买(最低/最高)次数为 3/13 次。"
              ),
              h(
                "p",
                { style: "color: #F25656; margin-bottom: 5px;" },
                "购买截至时间为6-30日。"
              ),
              h(
                "p",
                { style: "color: #F25656; margin-bottom: 5px;" },
                "购买需注意如下几点："
              ),
              h("p", { style: "color: #F25656;" }, ""),
              h(
                "p",
                { style: "color: #F25656; margin-bottom: 5px;" },
                "1. 购买 13 次仅支持未使用本方法 hack 过的用户，已 hack 过的用户请更换新号码尝试"
              ),
              h(
                "p",
                { style: "color: #F25656; margin-bottom: 5px;" },
                "2. 本方法不收取任何费用，仅提供次数提升的尝试"
              ),
              h(
                "p",
                { style: "color: #F25656; margin-bottom: 5px;" },
                "3. 指不定什么时候方法就失效了，失效前 hack 过的用户应该不受影响，但是失效后如果还有用户想 hack ，哒迈哒，请窒息。"
              ),
              h("p", { style: "color: #F25656;" }, ""),
              h(
                "p",
                { style: "color: #1890FF; margin-bottom: 5px;" },
                "如果需要购买13次，需要先自行在APP内购买完APP提供的三次后再来 hack 。如果直接进行 hack ，次数将锁定为10次，而不会是13次。"
              ),
              h("p", { style: "color: #F25656;" }, ""),
              h(
                "p",
                { style: "color: #F25656; font-weight: 700; font-size: 17px;" },
                "最后，且行，且珍惜，低调。方能细水长流。"
              ),
            ]),
            showCancelButton: false,
            confirmButtonText: "我知道了",
            beforeClose: (action, instance, done) => {
              if (action === "confirm") {
                this.showCbkDialog = true;
              }
              done();
            },
          });
        }
      }, 1000);
    },

    // 重置购买提示
    resetCkbTips() {
      this.showCkbTipsTime = 0;
      clearInterval(this.showCkbTipsTimeer);
      this.showCkbTipsTimeer = null;
    },

    // 获取手机验证码
    getMsg() {
      let that = this;
      let reg = /^1(3|4|5|6|7|8|9)\d{9}$/;
      if (!that.cbkMobile) {
        Message.error("手机号码不可为空");
      } else if (!reg.test(that.cbkMobile)) {
        Message.error("手机号码格式不正确，请检查");
      } else {
        // 跨域获取彩贝壳手机验证码
        that.API.requestData({
          method: "POST",
          baseURL: "/cbk/",
          subUrl: "ajax/user/sendVoteSmsCode.html",
          data: {
            mobile: that.cbkMobile,
            smsType: "wapLogin",
            skipVerify: "1",
          },
          success() {
            Message.success("验证码已发送，请稍候查收");
          },
        });
      }
    },

    // 登录彩贝壳
    login() {
      let that = this;
      let reg = /^1(3|4|5|6|7|8|9)\d{9}$/;

      if (!that.cbkMobile) {
        Message.error("手机号码不可为空");
      } else if (!reg.test(that.cbkMobile)) {
        Message.error("手机号码格式不正确，请检查");
      } else if (!that.cbkSmsCode) {
        Message.error("验证码不可谓空，请检查");
      } else {
        // 跨域登录彩贝壳，获取用户凭证
        that.API.requestData({
          method: "POST",
          baseURL: "/cbk/",
          subUrl: "ajax/user/loginNoPw.html",
          data: {
            mobile: that.cbkMobile,
            smsCode: that.cbkSmsCode,
            regSource: "web-fast",
          },
          success(res) {
            // 登录后返回数据示例
            // loginId: "19817445944",
            // token: "azc-D544C9a0oJdvmvd2qanxP7gAFQNVA_ES3SM_X0PyzCiZKL1BIbiYTrugldx_",
            // userId: "608f88d7e4b0d5dfa852c6ea",

            that.setCookie("wut", res.data.token, 1);

            that.setCookie("_UCID", res.data.userId, 1);

            that.setCookie("trace", "cbk", 1);

            that.setCookie("sajssdk_2015_cross_new_user", "1", 1);

            // that.setCookie(
            //   "sensorsdata2015jssdkcross",
            //   "%7B%22distinct_id%22%3A%22179fb9d0a2c47c-0064fe4b8c8be8-5437971-250125-179fb9d0a2dda1%22%2C%22first_id%22%3A%22%22%2C%22props%22%3A%7B%7D%2C%22%24device_id%22%3A%22179fb9d0a2c47c-0064fe4b8c8be8-5437971-250125-179fb9d0a2dda1%22%7D",
            //   1
            // );

            // that.setCookie("_UUID", "C00D8FA5-E931-4EA3-9059-024354451E5D", 1);

            // 跨域获取买币套餐信息
            $.post({
              url: "/cbk/mg/order/confirm.html",
              dataType: "json",
              crossDomain: true,
              xhrFields: { withCredentials: true },
              data: JSON.stringify({
                vo: {
                  bizType: "normal",
                  cpsToken: "MPG538",
                  isCps: true,
                  itemCode: "09098930806",
                },
              }),
              contentType: "application/json",
              success(res) {
                if (res.code !== 200) {
                  Message.error(res.message);
                } else {
                  // 跨域创建购买订单

                  let errorMsg = null;

                  // 循环十次并同时发送十个订单给彩贝壳服务器，能卡到多少就能成功多少，但是上限是10
                  for (let i = 0; i < 10; i++) {
                    $.post({
                      url: "/cbk/mg/order/create.html",
                      dataType: "json",
                      crossDomain: true,
                      xhrFields: { withCredentials: true },
                      data: JSON.stringify({
                        vo: {
                          bizType: "normal",
                          cpsToken: res.data.inviteCode, // 商品令牌
                          itemCode: res.data.itemCode, // 商品编号
                          isCps: true, // 不明
                          purchaseQuantity: 1, // 下单数量
                          unitPrice: res.data.price, // 商品价格
                          __orderCount__: i,
                        },
                      }),
                      contentType: "application/json",
                      success(res) {
                        that.showCbkDialog = false;
                        if (res.code !== 200) {
                          if (res.message !== errorMsg) {
                            errorMsg = res.message;
                            Message.error(res.message);
                          }
                        } else {
                          const h = that.$createElement;
                          that.$msgbox({
                            title: "咦？",
                            showClose: false,
                            distinguishCancelAndClose: true,
                            message: h("div", null, [
                              h(
                                "p",
                                { style: "" },
                                "卡订单成功，请打开APP查看"
                              ),
                            ]),
                            showCancelButton: false,
                            confirmButtonText: "好的",
                          });
                        }
                      },
                    });
                  }
                }
              },
            });
          },
        });
      }
    },
  },
};
</script>
